

.navbar{
    position: fixed;
    top: 0;
    width: 100%;
    height: 75px;
    z-index: 9;
    display: flex;
    justify-content: space-between;
    padding: 20px;
    transition: .5s;
}

.logo{
    position: fixed;
    left: 20px;
    width: 60px;
}

.avatar{
    position: fixed;
    right: 20px;
    width: 45px;
    border-radius: 50%;
    cursor: pointer;
}
.nav-link{
    position: fixed;
    right: 70px;
    color: #8c1115;
    font-weight: 500;
    text-decoration: none;
    opacity: 1;
    transition: .5s;
}
.nav-link:hover{
    transform: scale(1.05);
    opacity: .8;
    transition: .5s;
    
}
.nav_chnged{
    box-shadow: 0px 1px 18px 3px black;
    background-color: black;
    transition: ease-out;
    transition-duration: .5s;
}
@media only screen and (max-width:700px){
    .logo{
        width: 35px;
        top: 16px;
    }
    .avatar{
        width: 30px;
        top: 16px;
    }
    .navbar{
        height: 55px;
    }

}
