.row{
    padding: 20px;
    color: white;
    padding-left: 3%;
}
.posters{
    display: flex;
    margin-top: .5em;
    overflow-x: scroll;
    overflow-y: hidden;
    padding-left: 20px;
    padding-bottom: 5px;
    box-sizing: border-box;

}
.posters::-webkit-scrollbar{
    padding: 10px;
    height : 8px;
}

.poster{
    min-height: 200px;
    min-width: 300px;
    background-size: 300px 200px;
    background-repeat: no-repeat;
    cursor: pointer;
    margin-right: 10px;
    border-radius: 2px;
    transition:1s;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;  
}
.small-poster{
    min-height: 150px;
    min-width: 250px;
    background-size: 250px 150px;
    cursor: pointer;
    max-height: 150px;
    border-radius: 2px;
    margin-right: 10px;
    transition:1s;
    display: flex;
    flex-direction: column;
    justify-content: flex-end; 
}
.poster:hover{
    min-width: 350px;
    background-size: 350px 200px;
    transition:1s;
}
.small-poster:hover{
    min-width: 300px;
    background-size: 300px 150px;
    transition: 1s;
}
.video{
    margin-top: 1em;
    border-radius: 2px;
    transition: 1s;
}
.hide{
    visibility: hidden ;
}
.text{
    padding: .5em;
    opacity: 0;
    transition: 1s;
}
.rating{
    padding-left: 1em;
    opacity: 0;
    transition: 1s;

}
.android_txt, .android_rating{
    visibility: visible;
    opacity: 1;
}

.title-row{
    margin-bottom: 10px;
}
.soon{
    font-size: 20px;
    font-weight: 400;
}
@media only screen and (max-width:700px){
    .posters{
        padding-left: 0;
    }
    .poster{
        min-height: 150px;
        min-width: 250px;
        background-size: 250px 150px;
    }
}

