.banner-containor{
    background-image: linear-gradient(to top,rgba(0,0,0,.8) 0,rgba(0,0,0,0) 60%,rgba(0,0,0,.8) 100%);
    background-image: -webkit-gradient(linear,left bottom,left top,from(rgba(0,0,0,.8)),color-stop(60%,rgba(0,0,0,0)),to(rgba(0,0,0,.8)));
    
    height:100%;
}
.banner_in{
    
    background-repeat: no-repeat;
    background-size: cover;
    height: 70%;
    width: 100%;
    color: white;
}
.banner{
    height: 100%;
}

.content{
    padding: 3%;
    padding-top: 6%;
}

.title{
    font-weight: 600;
    font-size: 4rem;
    margin-bottom: .5em;
}
.desc{
    width: 50%;
    text-align: justify;
    line-height: 25px;
    margin-bottom: 2%;
}
.banner_butns{
    margin: 1em 0;
    display: flex;
    justify-content: left;
    width: 24%;
}
.banner_butns button{
    margin-right: 2em;
    background-color: rgba(51, 51, 51, .5);
    width: 40%;
    height: 26px;
    color: white;
    border: none;
    box-shadow: 0 0 12px 9px #00000070;
    border-radius: 3px;
    cursor: pointer;
    transition: 1s;
}
.banner_butns button:hover{
    color: black;
    background-color: #e6e6e6;

}
.fade{
    height: 6.4rem;
    background-image: linear-gradient(180deg,transparent,rgba(37,37,37,.61),#111);
    position: absolute;
    width: 100%;
    bottom: 30%;
}
@media only screen and (max-width: 1080px){
    h1{
        font-size: 20px;
    }
    .desc{
        width: 100%;
        max-height: 100%;
    }
    .banner_butns{
        width: 100%;
    }
    .fade{
        bottom: 30%;
    }
    .title{
        font-size: 50px;
    }
}
@media only screen and (max-width: 1535px){
    .fade{
        bottom: 30%;
    }
}
@media only screen and (max-height: 670px){
    .desc{
        width: 100%;
    }
    .content{
        padding: 20% 5% 5% 5%;
    }
}