.crop-top{
    position: fixed;
    height: 100%;
    width: 100%;
    z-index: 10;
    backdrop-filter: blur(8px);
}
.crop-box{
    position: absolute;
    width: 50%;
    height: 50%;
    background-color: white;
    top: 25%;
    left: 25%;
    z-index: 11;
    border-radius: 5px;
    box-shadow: 0 0 10px 1px #7f7f7f;
}
.crop-btns{
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    padding: 2%;
    height: 80%;
    align-items: flex-end;
    
}
.crop-btn{
    height: 35px;
    width: 90px;
    background-color: #222;
    outline: none;
    border: none;
    color: white;
    border-radius: 5px;
    position: relative;
}
.crop-close{
    height: 20%;
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    padding: 2%;
    
}
.crop-close svg{
    position: relative;
    z-index: 20;
}
@media only screen and (max-width:1000px){
    .crop-box{
        width: 90%;
        left: 5%;
    }
}