
/** Or line **/
.section{
    
    background-repeat: no-repeat;
    background-size: cover;
    height: 100%;
    width: 100%;
    color: white;
    background-image: url('https://assets.nflxext.com/ffe/siteui/vlv3/9b3267c9-5086-4550-92f1-eddc22a1f78e/8b897a27-47f0-42d0-bb50-10fdb72d34a2/IN-en-20211004-popsignuptwoweeks-perspective_alpha_website_large.jpg');
}
.containor{
    background-image: -webkit-gradient(linear,left bottom,left top,from(rgba(0,0,0,.8)),color-stop(60%,rgba(0,0,0,0)),to(rgba(0,0,0,.8)));
    background-image: linear-gradient(to top,rgba(0,0,0,.8) 0,rgba(0,0,0,0) 60%,rgba(0,0,0,.8) 100%);
}
.line{
    width: 47%;
    border: none;
    background-color: #222;
    height: 2px;
    margin-top: 3.5%;
}
.social-line{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}
/** social icons **/

.social-icons{
    display: flex;
    justify-content: center;
    cursor: pointer;

}
.social-svg-icon{
    fill: #bc1823;
}
.social-svg-icon:hover{
    fill: white;
}

/** Card **/
.card-login{
    height: 55%;
    background-color:#111 ;
}
/**signup link**/

.signup-link{
    text-align: center;
    color: grey;
}
.signup-link a{
    text-decoration: none;
    color: #bc1823;
}
@media only screen and (max-width:700px){
    .logo-box{
        display: none;
    }
    .signup-box{
        width: 100%;
    }
    .card-signup{
        width: 80%;
    }
}
