.pay-cont{
    height: 100%;
}
.pay-box{
    height: 80%;  
    display: flex;
    justify-content: center;
}
.pay-det{
    height: 100%;
    width: 50%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.pay-items{
    display: flex;
    justify-content: space-evenly;
    width: 45%;
    margin: 5%;
}
.line{
    width: 100%;
    margin-bottom: 5%;
    margin-top: 5%;
}
.pay-heading{
    padding-top: 5%;
    height: 20%;
    padding-left: 2%;
}
.gpay_but input{
    border: none;
    background-color: black;
    border-bottom: none;
    transition: .5s;
    border-top: 2px solid white;
    border-radius: 3px;
}
.gpay_but input:hover{
    transform: scale(1.05);
}
@media only screen and (max-width:700px){
    .pay-box{
        width: 100%;
    }
    .pay-det{
        width: 100%;
        justify-content: flex-start;
    }
    .pay-items{
        width: 100%;
    }
    .pay-heading{
        padding-top: 25%;
        padding-left: 3%;
    }
}