*{
    margin: 0;
    padding: 0;
}
html{
    height: 100%;
}
h1{
    font-weight: 500;
    font-size: 28px;
}
p{
    line-height: 25px;
}
body{
    scroll-behavior: smooth;
    height: 100%;
    background-color: #111;
    font-family: Asap,Camafortaa;
    box-sizing: border-box;
}
.app{
    height: 100%;
}
#root{
    height: 100%;
}
::-webkit-scrollbar{
    transition: .5s;
    width: 10px;
}
::-webkit-scrollbar-track{
    background-color: #111;
}
::-webkit-scrollbar-thumb{
    background-color: rgb(37, 37, 37);
    border-radius: 1px;
}
::-webkit-scrollbar-thumb:hover {
    background-color: rgb(57, 57, 57);
    transition: .5s;
  }
  .cards-top{
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    height: 100%;
  }
 
