.sub-cont{
    width: 100%;
    height: 100%;
}
.sub-containor{
    width: 100%;
    padding: 20px;
}
.sub-active-plan-top{
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    margin-top: 2%;
}
.sub-active-plan{
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
}
.sub-heading{
    height: 10%;
    width: 100%;
    margin: 3% 0 3% 0;
}
.sub-active-plan-cards{
    
}
.sub-card{
    border-radius: 10px;
    box-shadow: 0 0 20px 2px #222;
    height: 100%;
    width: 18%;
    display: flex;
    padding: 1%;
    flex-direction: column;
    align-items: center;
    background-color: #4e5157;
    float: left;
    margin-right: 1%;
    transition: .5s;
    margin-bottom: 3%;
}
.sub-card-heading{
    margin-bottom: 10%;
}
.sub-detail{
    margin-bottom: 10%;
}
.price{
    margin-bottom: 10%;
}
.sub-card-but{
    margin-bottom: 10%;
    width: 100%;
}
.sub-card-but input{
    transition: .5s;
    border: none;
    padding: 7%;
    cursor: pointer;
}

ol{
    list-style-type: none;
}
svg{
    color: black;
}
li{
    margin-bottom: 10%;
}
.sub-active-plan-cards{
    display: flex;
    flex-direction: row;
    justify-content: center;
}
.sub-avlble-plan-top{
}
.sub-avlble-plan{
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}
.sub-card:hover{
    transform: scale(1.1);
    margin-left: 1%;
    margin-right: 2%;
    
}
@media only screen and (max-width: 500px){
    .sub-card{
        width: 80%;
    }
}
@media only screen and (min-width: 500px) and (max-width: 700px){
    .sub-card{
        width: 50%;
    }
}
@media only screen and (min-width: 1000px) and (max-width: 1440px){
    .sub-card{
        width: 22%;
    }
}
@media only screen and (min-width: 700px) and (max-width: 1000px){
    .sub-card{
        width: 30%;
    }
}
@media only screen and (max-width: 700px){

    .avlble-plan-cards{
        flex-direction: column;
        justify-content: center;
        align-items: center;

    }
    .sub-heading{
        margin: 15% 0 10% 0;
    }
    .sub-card{
        margin-bottom: 5%;
    }
    .sub-card-but{
        width: 90%;
    }
}