div{
    box-sizing: border-box;
}
.body{

}
.section{
    
    background-repeat: no-repeat;
    background-size: cover;
    height: 100%;
    width: 100%;
    color: white;
    background-image: url('https://assets.nflxext.com/ffe/siteui/vlv3/9b3267c9-5086-4550-92f1-eddc22a1f78e/8b897a27-47f0-42d0-bb50-10fdb72d34a2/IN-en-20211004-popsignuptwoweeks-perspective_alpha_website_large.jpg');
}
.containor{
    background-image: -webkit-gradient(linear,left bottom,left top,from(rgba(0,0,0,.8)),color-stop(60%,rgba(0,0,0,0)),to(rgba(0,0,0,.8)));
    background-image: linear-gradient(to top,rgba(0,0,0,.8) 0,rgba(0,0,0,0) 60%,rgba(0,0,0,.8) 100%);
}
.section{
    width: 100%;
    height: 100%;
}
.containor{
    height: 100%;
    width: 100%;
}
.logo-box{
    height: 100%;
    width: 50%;
    float: left;
    display: flex;
    flex-direction: column;
    justify-content: center;
    
}
.signup-box{
    height: 100%;
    width: 50%;
    float: left;
    
}
.logo-img{
    width: 30%;
    animation:fadeIn 2s;
}
@keyframes fadeIn {
    0% {opacity:0;}
    100% {opacity:1;}
    
  }
.logo-signup{
    display: flex;
    flex-direction: row;
    justify-content: center;
}

/** Signup Card **/

.signup-box{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.card-signup{
    width: 50%;
    height: 70%;
    border-radius: 10px;
    box-shadow: 0px 0px 20px 3px #222;
    padding: 1em;
    background-color:#111 ;
}
.card-heading{
    text-align: center;
    height: 15%;
}
.card-heading h1{
    font-weight: 500;
}
input{
    width: 100%;
    height: 100%;
    box-sizing: border-box;
    padding: 10px;
    border: solid 2px #111;
    border-bottom: solid 2px #222;
    background-color: #111;
    transition: .2s;
    color: white;
    border-radius: 2px;
}
input:focus{
    outline: none;
    box-shadow: 0 0 6px 3px #222;
}
input:hover{
    border: solid 2px #222;
}
.name{
    width: 48%;
}
.name2{
    margin-left: 4%;
}
.card-details{
    height: 100%;
}
.details{
    height: 85%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.input-box{
    height: 12%;
}
.sub-button{
    height: 12%;
}
.sub-button input{
    background-color: #bc1823;
    color: white;
    height: 100%;
}
.sub-button input:active{
    border: none;
    transform: scale(.95);
}
.sub-button input:hover{
    border: none;
    
}